import React from 'react';
import Load from "../loading.svg";
import look from "../looking.svg";
import Navigation from "./navigation";


import background from "../background.png";

const Loading_screen = (props) => {
    return (
        <section className="homepage center">
                    <Navigation onSwitch={props.changes} />
                    
                    <img className="backimg backimg2" src={background} alt="" />
                   
                    <img className="looking" src={look} alt=""/>
                    <div className="loading_container">
                    <h2 className="loading_title">Loading</h2>
                    <img className="loading_img" src={Load} alt=""/>

                    </div>
        </section>

    );
}
 
export default Loading_screen;