import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import Navigation from "./navigation";
import phone from "../phone.svg";
import Logo from "../logo.svg";
import background from "../background.png";


class Home extends Component {
  state = {
  };

search=()=>{
  let valid=true;
 let input= document.querySelector(".search");
  if(this.state.recherche===undefined ||this.state.recherche.trim()==='' ){
    valid=false;
    input.style.border="2px solid red";
   
  }
  if(valid){
    this.props.onPageChange(3,this.state.recherche)
  }
  else{

  }
  
}
  handleChange = (e) => {
    let input= document.querySelector(".search");
    input.style.border="none";
    let name = e.target.name;
    let value = e.target.value;
    this.setState({
      ...this.state,
      [name]: value,
    });
  };

  render() {
    return (
      <section className="homepage">
        <Navigation onSwitch={this.props.onPageChange} />
      
        <img className="illustration" src={phone} alt="" />

        <img className="backimg backimg2" src={background} alt="" />
        <div className="home_page_description center">
          <img className="logo" src={Logo} alt="" />
          <h2 className="description_title">Amawal </h2>
          <h4 className="description_details">
            {" "}
            <span>Amawal</span> Est un Dictionaire bilingue Kabyle-Français Qui
            Regroupe Plus de 2000 Mots traduits mais également des exemples et
            définitions afin de connaitre le sens ou apprendre du vocabulaire
          </h4>
          <div className="form_container center">
            <input
              className="search"
              type="text"
              placeholder="Taper Un Mot"
              name="recherche"
              onChange={this.handleChange}
              id=""
            />
            <div onClick={this.search} className="btn center">
              Rechercher
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Home;
