import React, { Component } from "react";
import {Link} from "react-router-dom"
class Navigation extends Component {
  state = {};
  openSideBar() {
    document.querySelector("nav").classList.toggle("open");
    document.querySelector(".dark").classList.toggle("block");
  }
  closeSideBar() {
    document.querySelector("nav").classList.remove("open");
    document.querySelector(".dark").classList.remove("block");
  }
  render() {
    return (
      <React.Fragment>
        <div onClick={this.closeSideBar} className="dark"></div>
        <div onClick={this.openSideBar} className="menu_btn">
          <div></div>
          <div></div>
          <div></div>
        </div>
        <nav className="center">
          <ul className="navList center">
            <li onClick={()=>this.props.onSwitch(1)} className="List_item">Home</li>
            <li onClick={()=>this.props.onSwitch(2)}className="List_item">Contribute</li>
            <li onClick={()=>this.props.onSwitch(3)} className="List_item">About </li>
          </ul>
        </nav>
      </React.Fragment>
    );
  }
}

export default Navigation;
