import "./App.css";
import React, { Component } from "react";
import Home from "./components/home";
import Contribution from "./components/contribution";
import Result from "./components/result";

class App extends Component {
  state = {
    current_page: 1,
  };
  changePage = (page, data) => {
    if (page === 3) {
      this.setState({ current_page: page, data });
    } else {
      this.setState({ current_page: page });
    }
  };
  render() {
    if (this.state.current_page == 1) {
      return <Home onPageChange={this.changePage} />;
    }
    if (this.state.current_page == 2) {
      return <Contribution onPageChange={this.changePage} />;
    }
    if (this.state.current_page == 3) {
      return <Result onPageChange={this.changePage} data={this.state.data} />;
    }
    else{
      return <h1>error</h1>
    }
  }
}

export default App;
