import React, { Component } from "react";
import background from "../background.png";
import girl from "../searchgirl.svg";
import boy from "../searchboy.svg";
import Navigation from "./navigation";
import firebasedb from "../firebase";
import Loading from "./loading_screen";
import wind from "../wind.svg";
import globe from "../globe.svg";

class Results extends Component {
  state = {
    DBdata: [],
  };

  componentDidMount() {
    let found = false;
    let data = {};
    let DBdata = [];
    firebasedb.child("Mot").on("value", (snapshot) => {
      if (snapshot.val() != null) {
        data = { ...snapshot.val() };
        Object.keys(data).map((id) => {
          if (data[id].mot.toUpperCase() === this.props.data.toUpperCase()) {
            found = true;
            DBdata.push(data[id]);
            this.setState({ DBdata });
          }
        });
      } else {
      }
      if (!found) console.log("not found");
    });
  }

  render() {
    if (this.state.DBdata.length === 0) {
      return <Loading changes={this.props.onPageChange} />;
    }

    return (
      <section className="result_page">
        <Navigation onSwitch={this.props.onPageChange} />
        <img className="backimg backimg2" src={background} alt="" />
        <img className="search_girl" src={girl} alt="" />
        <img className="wind" src={wind} alt="" />
        <img className="globe" src={globe} alt="" />

        <div className="data">
          <img className="search_boy" src={boy} alt="" />
          <div className="data_heading center2">
            <i className="fas fa-search"></i>
            {this.state.DBdata[0].mot}
          </div>
          <div className="data_element">
            <h3 className="element_title">
              <i className="fas fa-thumbtack"></i> L'origine Du Mot :
            </h3>
            <h2 className="element_details">{this.state.DBdata[0].origine}</h2>
          </div>
          <div className="data_element">
            <h3 className="element_title">
              {" "}
              <i className="fas fa-map-marker-alt"></i> Wilaya D'origine :
            </h3>
            <h2 className="element_details">{this.state.DBdata[0].wilaya}</h2>
          </div>
          <div className="data_element">
            <h3 className="element_title">
              <i className="fas fa-book"></i> Synonymes :
            </h3>
            <h2 className="element_details">
              {this.state.DBdata[0].Synonymes}
            </h2>
          </div>
          <div className="data_element">
            <h3 className="element_title">
              <i className="fas fa-sticky-note"></i> Definition :
            </h3>
            <h2 className="element_details">
              {this.state.DBdata[0].definition}
            </h2>
          </div>
        </div>
      </section>
    );
  }
}

export default Results;
