import React, { Component } from "react";
import firebasedb from "../firebase";
import Navigation from "./navigation";
import thanks from "../thanks1.svg";
import thanks2 from "../thanks2.svg";
import thanks3 from "../thank you.svg";
import background from "../background.png";

class Contribution extends Component {
  state = {
    mot: "",
    origine: "",
    Synonymes: "",
    wilaya: "",
    definition: "",
  };
  handleChange = (e) => {
    e.target.style.borderBottom = "2px solid #f47458";
    let name = e.target.name;
    let value = e.target.value;
    this.setState({
      ...this.state,
      [name]: value,
    });
  };
  closePopup=()=>{
    let blocker = document.querySelector(".dark");
    let error_popup = document.querySelector(".form_error");
    error_popup.style.top = "150%";
    error_popup.style.display = "none";
    blocker.classList.remove("block");
    blocker.style.zIndex = "5";

    console.log(blocker)
  }
  click = () => {
    let valid = 0;
    let inputs = document.querySelectorAll("input");
    let textarea = document.querySelector("textarea");
    let error_popup = document.querySelector(".form_error");
    let blocker = document.querySelector(".dark");
    blocker.addEventListener("click", () => {
      error_popup.style.top = "150%";
      blocker.style.zIndex = "5";
    });
    inputs.forEach((input) => {
      if (input.value.trim() === "") {
        input.style.borderBottom = "3px solid red";
        valid++;
      }
    });
    if (textarea.value.trim() === "") {
      textarea.style.borderBottom = "3px solid red";
      valid++;
    }
    if (valid == 0) {
      firebasedb.child("Mot").push(this.state, (err) => {
        if (err) console.log(err);
        else {
          this.setState({
            mot: "",
            origine: "",
            Synonymes: "",
            wilaya: "",
            definition: "",
          });
          console.log(this.state);
        }
      });
      
      error_popup.style.display = "block";
      error_popup.style.top = "50%";
      blocker.classList.add("block");
      blocker.style.zIndex = "55";
     
    } else {
     
      console.log("not valid");
    }
  };
  render() {
    return (
      <section className="contribution">
        <img className="backimg backimg2" src={background} alt="" />

        <Navigation onSwitch={this.props.onPageChange} />
        <h2 className="contribution_logo">Amawal</h2>

        <div className="contribution_form_container">
          <div className="input_container">
            <label htmlFor="mot">Le Mot</label> <br />
            <input
              placeholder="Entrer le Mot"
              type="text"
              name="mot"
              value={this.state.mot}
              onChange={this.handleChange}
            />
          </div>
          <div className="input_container">
            <label htmlFor="mot">L'origine</label> <br />
            <input
              placeholder="Entrer L'origine Du Mot"
              type="text"
              name="origine"
              value={this.state.origine}
              onChange={this.handleChange}
            />
          </div>
          <div className="input_container">
            <label htmlFor="mot">Les Synonymes</label> <br />
            <input
              placeholder="Entrer Les Synonymes Du Mot"
              type="text"
              name="Synonymes"
              value={this.state.Synonymes}
              onChange={this.handleChange}
            />
          </div>

          <div className="input_container">
            <label htmlFor="mot">Wilaya</label> <br />
            <input
              placeholder="Entrer le mot"
              type="text"
              name="wilaya"
              value={this.state.wilaya}
              onChange={this.handleChange}
            />
          </div>
          <div className="input_container text">
            <label htmlFor="mot">La Definition</label> <br />
            <textarea
              placeholder="Entrer La Definition Du Mot"
              type="text"
              name="definition"
              value={this.state.definition}
              onChange={this.handleChange}
            />
          </div>
          <div className="input_container text left">
            <div onClick={this.click} className="btn center">
              CONTRIBUER
            </div>
          </div>

          <img className="thanks2" src={thanks2} alt="" />
          <img className="thanks1" src={thanks} alt="" />
        </div>
        <div className="form_error">
          <div className=" thanks_container center2">
            Contribution Envoyer !
          </div>
          <div className="thanks_msg">
          <img className="thanks3" src={thanks3} alt="" />
          <div style={{justifyContent:"space-around"}} className="center">
          <h2 className="thanks_msg2">Merci Pour votre Contribution ! <br/>Votre contribution est en attente de validation par un administrateur</h2>
          <div className="btn_container center2">
          <div onClick={this.closePopup} style={{backgroundColor:"#2e8b76"}} className="btn center2 cbtn">Fermer</div>
          </div>
          </div>
          </div>
         
        </div>
      </section>
    );
  }
}

export default Contribution;
